/**
 * 
 *
 * @class
 * @param any value 
 * @param string kind var, let, const
 * @method set 
 * @method get 
 */
class SimpleVal {
  constructor(value, kind = "") {
    this.value = value;
    this.kind = kind;
  }

  set(value) {
    // const
    if (this.kind === "const") {
      throw new TypeError("Assignment to constant variable");
    } else {
      this.value = value;
    }
  }

  get() {
    return this.value;
  }
}

/**
 * 
 *
 * @class
 * @param any obj 
 * @param prop any 
 * @method set 
 * @method get 
 */
class MemberVal {
  constructor(obj, prop) {
    this.obj = obj;
    this.prop = prop;
  }

  set(value) {
    this.obj[this.prop] = value;
  }

  get() {
    return this.obj[this.prop];
  }
}

module.exports.SimpleVal = SimpleVal;
module.exports.MemberVal = MemberVal;
